<template>
    <div class="DatePicker">
        <p class="readonly-value" v-if="readonly">{{ formattedDate }}</p>
        <v-date-picker
            v-if="!readonly"
            :value="value"
            @input="$emit('input', $event)"
            color="gray"
            is-dark
            :popover="{ visibility: 'click' }"
            >
            <button type="button" class="trigger-button">
                {{ formattedDate }}
            </button>
        </v-date-picker>
    </div>
</template>

<script>
    import DatePicker from 'v-calendar/lib/components/date-picker.umd';

    /**
     * Formats the date like 00.00.0000
     * @param {Date} date
     */
    function formatDate(date) {
        const day = date.getDate();
        const monthIndex = date.getMonth() + 1;
        const year = date.getFullYear();

        const pad = number => (number > 9 ? `${number}` : `0${number}`);

        return `${pad(day)}-${pad(monthIndex)}-${year}`;
    }

    export default {
        name: 'DatePicker',
        components: {
            'v-date-picker': DatePicker,
        },
        props: {
            value: {
                required: true,
            },
            readonly: {
                type: Boolean,
                default: false,
            },
        },
        computed: {
            formattedDate() {
                return formatDate(this.value);
            },
        },
    };
</script>

<style lang="scss">
    .DatePicker {
        .vc-popover-content-wrapper {
            left: -92px !important;
        }

