<template>
    <div class="Dropdown" :class="{ 'active': isActive }">
        <div class="dropdown-trigger" @click="click">
            <p>{{ active.label }}</p>
            <font-awesome-icon
                class="icon" 
                :icon="isActive ?
                ['fas', 'angle-up'] : ['fas', 'angle-down']" />
        </div>
        <div class="dropdown-menu" role="menu">
            <div v-for="(item, index) in items"
                :key="index"
                class="dropdown-item"
                :class="{ active: item.label != active.label }"
                @click="choose(item)"
                >
                {{ item.label }}
            </div>
        </div>
    </div>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';

    library.add(faAngleDown, faAngleUp);

    export default {
        name: 'Dropdown',
        props: {
            items: {
                type: Array,
                required: true,
            },
        },
        data() {
            return {
                isActive: false,
                active: this.items[0],
            };
        },
        methods: {
            click() {
                this.isActive = !this.isActive;
            },
            choose(chosen) {
                this.isActive = false;
                this.active = chosen;
                // this.$emit('dropdown', chosen);
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .Dropdown {
        position: relative;

        .dropdown-trigger {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 5px;
            min-width: 60px;
            border-bottom: 1px solid white;
            cursor: pointer;

            p {
                margin: 0;
                margin-right: 5px;
                color: white;
            }

            .icon {
                font-size: 1.5em;
                color: white;
            }
        }

        .dropdown-menu {
            pointer-events: none;
            position: absolute;
            top: 35px;
            width: 100%;
            opacity: 0;
            transition: 0.1s ease-out opacity;

            .dropdown-item {
                position: relative;
                display: block;
                padding: 2px 5px;
                color: $dark-text;
                border-bottom: 1px solid $dark-text;
                background-color: black;
                cursor: pointer;
                z-index: 10;

                &:hover {
                    color: white;
                }
            }
        }

        &.active {
            .dropdown-menu {
                pointer-events: unset;
                opacity: 1;
            }

            .dropdown-trigger {
                border-color: $gold;

                .icon {
                    font-size: 1.5em;
                    color: $gold;
                }
            }
        }
    }

</style>
