<template>
    <div>
        <MainPageHeader/>
        <PageContent class="content">
            <header>
                <h1 class="white">Zarządzaj grupą</h1>
                <AddButton
                    v-if="canAddMembers"
                    :editable="addMemberPanel"
                    @click="addMember"
                    />
            </header>
            <div class="add-panel" v-if="addMemberPanel">
                <ValidationObserver v-slot="{ invalid }">
                    <form @submit.prevent="save(invalid)">
                        <ValidationProvider rules="required" v-slot="{ errors }">
                            <DefaultInputField
                                :invalid="!!errors.length"
                                v-model="field.email"
                                placeholder="email"
                                type="text"
                                />
                            <InputError v-if="errors.length" :message="errors[0]" />
                        </ValidationProvider>
                        <ValidationProvider name="confirm" rules="required" v-slot="{ errors }">
                            <DefaultInputField v-model="field.password"
                                :invalid="!!errors.length"
                                placeholder="hasło"
                                type="password"
                                />
                            <InputError v-if="errors.length" :message="errors[0]" />
                        </ValidationProvider>
                        <ValidationProvider rules="required|password:@confirm" v-slot="{ errors }">
                            <DefaultInputField
                                :invalid="!!errors.length"
                                v-model="field.passwordConfirm"
                                placeholder="powtórz hasło"
                                type="password"/>
                            <InputError v-if="errors.length" :message="errors[0]" />
                        </ValidationProvider>
                        <SmallButton
                            type="submit"
                            border-color="primary">
                            Zapisz
                        </SmallButton>
                    </form>
                </ValidationObserver>
            </div>
            <div class="elements">
                <AccountManageRow
                    v-for="member in members"
                    :key="member.key"
                    :account="member"
                    :editable="!!memberEditModes[member.key]"
                    @edit="toggleEdit(member.key)"
                    @save="saveChanges(member.key, $event)"
                    />
            </div>
        </PageContent>
    </div>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faPlus, faTimes, faPen } from '@fortawesome/free-solid-svg-icons';
    import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
    import { required } from 'vee-validate/dist/rules';
    import DefaultInputField from '@/components/input/DefaultInputField.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import AddButton from '@/components/input/AddButton.vue';
    import InputError from '@/components/input/InputError.vue';
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageContent from '../components/generic/PageContent.vue';
    import AccountManageRow from '../components/tab2/AccountManageRow.vue';
    import { getGroupService } from '../services/groupService';
    import { getAccountService } from '../services/accountService';
    import { ADMIN, USER, SUBUSER } from '../types/role.types';
    import { getAuthService } from '../services/authService';
    import { limitView } from '../helpers/permissionHelper';

    library.add(faPlus, faTimes, faPen);

    extend('password', {
        params: ['target'],
        validate(value, { target }) {
            return value === target;
        },
        message: 'Hasła muszą być takie same.',
    });

    extend('required', {
        ...required,
        message: 'Pole jest wymagane',
    });

    export default {
        name: 'Tab2',
        components: {
            MainPageHeader,
            PageContent,
            AccountManageRow,
            DefaultInputField,
            SmallButton,
            AddButton,
            ValidationProvider,
            ValidationObserver,
            InputError,
        },
        computed: {
            canAddMembers() {
                return this.role === ADMIN || this.role === USER;
            },
        },
        data() {
            return {
                members: [],
                field: {
                    email: '',
                    password: '',
                    passwordConfirm: '',
                },
                role: null,
                addMemberPanel: false,
                memberEditModes: {},
            };
        },
        async mounted() {
            if (await limitView(this.$router, [ADMIN, USER, SUBUSER])) {
                return;
            }

            this.role = await getAuthService().getRole();
            this.members = await getGroupService().getFellowGroupMembers();
        },
        methods: {
            addMember() {
                this.addMemberPanel = !this.addMemberPanel;
                this.field = {
                    email: '',
                    password: '',
                    passwordConfirm: '',
                };
                this.memberEditModes = {};
            },
            async save(invalid) {
                if (!invalid) {
                    const result = await getAccountService().addSubAccount({
                        email: this.field.email,
                        parentUserKey: await getGroupService().getGroupMemberKey(),
                    }, this.field.password);

                    if (result) {
                        this.members = await getGroupService().getFellowGroupMembers();

                        this.addMemberPanel = !this.addMemberPanel;
                        this.field = {
                            email: '',
                            password: '',
                            passwordConfirm: '',
                        };
                    }
                }
            },
            toggleEdit(memberKey) {
                this.addMemberPanel = false;
                this.memberEditModes = {
                    [memberKey]: !this.memberEditModes[memberKey],
                };
            },
            async saveChanges(memberKey, changes) {
                await getAccountService().updateAccount({
                    key: memberKey,
                    email: changes.email,
                });

                await getAccountService().updatePassword(memberKey, null, changes.password);

                this.members = await getGroupService().getFellowGroupMembers();

                this.memberEditModes = {
                    [memberKey]: false,
                };
            },
        },
    };
</script>

