    @import "@/assets/scss/_variables.scss";
    @import "@/assets/scss/bulma/projectBulma.scss";

    .content {
        max-width: 600px;

        header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;

            h1 {
                color: white;
                font-size: 2em;
                font-weight: normal;
                margin: 0;
            }
        }

        .add-panel {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            max-width: 300px;
            margin: 16px auto;

            .SmallButton {
                display: block;
                margin: 30px auto;

                width: 100%;
            }
        }

        .elements {
            .row {
                display: flex;
