<template>
    <div class="AccountManageRow">
        <div class="row">
            <RoleTag :account="account" />
            <div class="email">{{ account.email }}</div>
            <SmallButton v-if="isAdmin" border-color="white" @click="logOver()">Przeloguj</SmallButton>
            <EditButton v-else-if="isNormalUser" :editable="editable" @click="$emit('edit')" />
        </div>
        <div class="edit-panel" v-if="editable">
            <ValidationObserver v-slot="{ invalid }">
                <form @submit.prevent="applyChanges(invalid)">
                    <ValidationProvider rules="required" v-slot="{ errors }">
                        <DefaultInputField
                            v-model="email"
                            :invalid="!!errors.length"
                            placeholder="email"
                            type="text"
                            />
                        <InputError v-if="errors.length" :message="errors[0]" />
                    </ValidationProvider>
                    <ValidationProvider name="confirm" rules="required" v-slot="{ errors }">
                        <DefaultInputField
                            v-model="password"
                            :invalid="!!errors.length"
                            placeholder="hasło"
                            type="password"
                            />
                        <InputError v-if="errors.length" :message="errors[0]" />
                    </ValidationProvider>
                    <ValidationProvider rules="required|password:@confirm" v-slot="{ errors }">
                        <DefaultInputField
                            v-model="password_comfirm"
                            :invalid="!!errors.length"
                            placeholder="powtórz hasło"
                            type="password"/>
                        <InputError v-if="errors.length" :message="errors[0]" />
                    </ValidationProvider>
                    <SmallButton
                        type="submit"
                        border-color="primary">
                        Zapisz
                    </SmallButton>
                </form>
            </ValidationObserver>
        </div>
    </div>
</template>

<script>
    import { ValidationProvider, ValidationObserver } from 'vee-validate';
    import DefaultInputField from '@/components/input/DefaultInputField.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import EditButton from '@/components/input/EditButton.vue';
    import RoleTag from '../RoleTag.vue';
    import InputError from '../input/InputError.vue';
    import { getAuthService } from '../../services/authService';
    import { ADMIN, USER } from '../../types/role.types';
    
    export default {
        name: 'AccountManageRow',
        components: {
            RoleTag,
            DefaultInputField,
            SmallButton,
            EditButton,
            ValidationProvider,
            ValidationObserver,
            InputError,
        },
        props: {
            account: {
                required: true,
            },
            editable: {
                type: Boolean,
                required: true,
            },
        },
        data() {
            return {
                email: '',
                password: '',
                password_comfirm: '',
                role: null,
            };
        },
        computed: {
            isAdmin() {
                return this.role === ADMIN;
            },
            isNormalUser() {
                return this.role === USER;
            },
        },
        methods: {
            async logOver() {
                const result = await getAuthService().manageUser(this.account.key);

                if (result) {
                    this.$router.go();
                }
            },
            applyChanges(invalid) {
                if (!invalid) {
                    this.$emit('save', {
                        email: this.email,
                        password: this.password,
                    });
                }
            },
        },
        async mounted() {
            this.role = await getAuthService().getRole();

            this.$watch('editable', () => {
                if (this.editable) {
                    this.email = `${this.account.email}`;
                }
            });
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .AccountManageRow {
        .row {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .email {
                flex: 1 1;
                padding: 5px 0;
                margin: 10px 0;
                color: $dark-text;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .edit-panel {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            max-width: 300px;
            margin: 16px auto;

            .SmallButton {
                display: block;
                margin: 30px auto;

                width: 100%;
            }
        }
    }

    @media only screen and (max-width: $phone-max-width) {
        .AccountManageRow {
            .row {
                padding-left: 30px;
                margin-right: -30px;
            }
        }
    }
</style>
