<template>
    <div class="AccessRow">
        <div class="email">
            {{ account.email }}
        </div>
        <div class="date">
            <DatePicker :readonly="!editable" v-model="editableDate" />
        </div>
        <div class="actions" v-if="isAdmin">
            <SmallButton 
                class="edit-button"
                :icon="editable ? 'times' : 'pen'"
                :border-color="editable ? 'white' : 'primary'"
                @click="toggleEdit">
                {{ editable ? 'Anuluj' : 'Zmień' }}
            </SmallButton>
            <SmallButton v-if="editable" class="save-button" icon="check" border-color="primary" @click="saveChanges">
                Zapisz
            </SmallButton>
        </div>
        <div class="actions lengthen-panel" v-else>
            <Dropdown :items="items"/>
            <SmallButton class="change-access-button" border-color="primary" @click="lengthenAccess">
                Przedłuż
            </SmallButton>
        </div>
    </div>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faPen, faTimes, faCheck } from '@fortawesome/free-solid-svg-icons';
    import DatePicker from '../input/DatePicker.vue';
    import SmallButton from '../input/SmallButton.vue';
    import Dropdown from '../input/Dropdown.vue';
    import { ADMIN } from '../../types/role.types';
    import { getAuthService } from '../../services/authService';

    library.add(faPen, faTimes, faCheck);

    export default {
        name: 'AccessRow',
        components: {
            DatePicker,
            SmallButton,
            Dropdown,
        },
        props: {
            account: {
                required: true,
            },
            editable: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                editableDate: new Date(),
                role: null,
                items: [
                    { label: '12' },
                    { label: '6' },
                    { label: '1' },
                ],
            };
        },
        computed: {
            isAdmin() {
                return this.role === ADMIN;
            },
        },
        methods: {
            toggleEdit() {
                this.$emit('edit');
            },
            saveChanges() {
                this.$emit('save', {
                    expiryDate: this.editableDate,
                });
            },
            lengthenAccess() {
                // TODO
            },
        },
        async mounted() {
            this.editableDate = new Date(this.account.expiryDate);

            this.$watch('editable', () => {
                if (!this.editable) {
                    this.editableDate = new Date(this.account.expiryDate);
                }
            });

            this.role = await getAuthService().getRole();
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .AccessRow {
        display: grid;
        grid-template-areas: 'email date actions';
        grid-template-columns: 1fr 1fr 210px;
        margin: 1em 0;

        .email, .date {
            color: $dark-text;
            word-break: break-all;
        }

        .email {
            grid-area: email;
        }

        .date {
            grid-area: date;
            text-align: center;
        }

        .actions {
            grid-area: actions;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
        }
    }

    @media only screen and (max-width: $phone-max-width) {
        .AccessRow {
            grid-template-columns: 1fr 1fr 80px;

            .lengthen-panel {
                align-items: stretch;
                flex-direction: column;

                .change-access-button {
                    margin-top: 5px;
                    min-width: unset;
                }
            }
        }
    }
</style>
