import { accountDatabase } from './accountDatabase';
import { getLoggedInEmail, getManagedUser } from './sessionProps';
import { deepCopyAccounts } from './mockUtils';
import { waitForSeconds } from '../../helpers/asyncHelper';
import { ADMIN } from '../../types/role.types';
import { passwordDatabase } from './passwordDatabase';

function getGroupMembers(accounts, member, includeSelf) {
    if (member.parentUserKey === undefined) {
        // This is a normal user.
        if (includeSelf) {
            return deepCopyAccounts([
                ...accounts.filter(a => a.parentUserKey === member.key),
                member,
            ]);
        }
        
        return deepCopyAccounts(accounts.filter(a => a.parentUserKey === member.key));
    }

    const parentAccount = accounts.find(a => a.key === member.parentUserKey);
    if (parentAccount === undefined) {
        // Not a normal user, but no parent account.
        return [];
    }

    // Sub-user
    if (includeSelf) {
        return deepCopyAccounts([
            ...accounts.filter(a => a.parentUserKey === parentAccount.key),
            parentAccount,
        ]);
    }
    
    return deepCopyAccounts([
        ...accounts.filter(a => a.parentUserKey === parentAccount.key && a.key !== member.key),
        parentAccount,
    ]);
}

/* eslint-disable import/prefer-default-export */
export const GroupServiceMock = {
    async getGroupMemberKey() {
        const accounts = Object.keys(accountDatabase).map(key => ({
            ...accountDatabase[key],
            key,
        }));

        const loggedInEmail = getLoggedInEmail();

        let relativeAccount;

        if (passwordDatabase[loggedInEmail] && passwordDatabase[loggedInEmail].role === ADMIN) {
            relativeAccount = accounts.find(a => a.key === getManagedUser());
        } else {
            relativeAccount = accounts.find(a => a.email === loggedInEmail);
        }

        if (relativeAccount === undefined) {
            return null;
        }

        return relativeAccount.parentUserKey || relativeAccount.key;
    },

    /**
     * 
     * @param {(boolean|undefined)} includeSelf 
     */
    async getFellowGroupMembers(includeSelf) {
        // Faking a server response time.
        await waitForSeconds(0.1 + Math.random() * 0.2);

        const accounts = Object.keys(accountDatabase).map(key => ({
            ...accountDatabase[key],
            key,
        }));

        const loggedInEmail = getLoggedInEmail();

        if (passwordDatabase[loggedInEmail] && passwordDatabase[loggedInEmail].role === ADMIN) {
            const managedAccountKey = getManagedUser();
            const managedAccount = accounts.find(a => a.key === managedAccountKey);

            if (managedAccount !== undefined) {
                return getGroupMembers(accounts, managedAccount, includeSelf);
            }

            return [];
        }
        
        const loggedInAccount = accounts.find(a => a.email === loggedInEmail);
        
        if (loggedInAccount === undefined) {
            // Not logged in.
            return [];
        }

        return getGroupMembers(accounts, loggedInAccount, includeSelf);
    },
    
};
